body {
    background-image: url("bg.webp");
    
   }
.dropbtn {
    border-radius: 10px;
    color: rgb(5, 0, 27);
    padding: 16px;
    font-size: 32px;
    border: none;
    background: rgb(255,151,0);
    border: none;
    z-index: 1;
    box-shadow: 5px 5px rgb(109, 26, 0);
  }
  .dropbtn:hover{
    font-size: 34px;;
  }
  
  .dropdown {

    margin-top:40vh;
    margin-left: 45%;
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    font-size: 18px;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 22px;
    text-decoration: none;
    display: block;
    background-color: beige;
  }
  
  .dropdown-content a:hover {background-color: rgb(129, 247, 255);
    font-size: 22px;
}
  
  .dropdown:hover .dropdown-content {display: block;}
  
